.userInfoContainer {
    margin: 80px 10px 20px 12px;
}

.information {
    display: flex;
    align-items: center;
}

.userImg {
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 5px;
    margin-right: 10px;
    img {
        width: 100%;
        height: 100%;
    }
}

.userNameContact {
    margin-bottom: 8px;
    p {
        font-size: 14px;
    }
}

.userContact {
    display: flex;
    align-items: center;
    div {
        display: flex;
        align-items: center;
        img {
            margin-right: 5px;
        }
    }
}

.userDescription {
    margin-top: 12px;
    textarea {
        width: 100%;
        height: 72px;
        border: 0.5px solid $active-color;
        border-radius: 5px;
        background-color: transparent;
    }
}

.ProfileMenu {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 5px 0;
    background-color: $base-background-color;
    border-radius: 5px 5px 0 0;
    button {
        border: none;
        background-color: transparent;
        font-size: 12px;
        font-weight: 100;
        color: $base-text-color;
    }
}

.containerProfileComp {
    height: 65vh;
    padding: 0 20px 30px 20px;
    background-color: $base-background-color;
    overflow-y: auto;
    &::-webkit-scrollbar {
        display: none;
    }
}

.personal {
    h6 {
        font-size: 14pt;
        font-weight: normal;
        color: $base-text-color;
        margin-top: 13px;
        margin-bottom: 9px;
    }
    .tags {
        display: flex;
        align-items: center;
        align-items: center;
        flex-wrap: wrap;
        div {
            margin-right: 8px;
            margin-bottom: 9px;
            font-size: 8px;
            padding: 2px 6px;
            border-radius: 5px;
        }
        .addTag {
            background: #B9B9B9;
            cursor: pointer;
        }
    }
    .roadmap {
        border-radius: 10px;
        background-color: #353b49;
        color: #fff;
        padding: 6px;
        position: relative;
        
        .creationPopup {
            position: absolute;
            right: 15px;
            bottom: 55px;
            width: min(260px, 90%);
            height: auto;
            background: $base-background-color;
            border-radius: 10px;
            padding: 10px 15px;
            display: flex;
            flex-direction: column;
            gap: 10px;
            justify-content: space-between;
            box-shadow: 0 4px 4px #0000004d;
        }

        .flatButton {
            position: absolute;
            right: 15px;
            bottom: 15px;
            width: 30px;
            height: 30px;
            border-radius: 15px;
            background-color: $active-color;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        table {
            height: 30vh;
            display: block;
            overflow-y: auto;

            &::-webkit-scrollbar {
                display: none;
            }
            
            tr {
                height: 45px;
                display: block;
    
                td {
                    padding: 0 8px;
                }
                .date {
                    font-size: 8pt;
                    color: #b9b9b9;
                }
                .name {
                    font-size: 12px;
                }
                .editButton {
                    width: 15px;
                    height: 15px;
                }
                img.roadmapPoint {
                    height: 46.5px;
                }
            }
        }
    }
}

.tagsPopup {
    min-height: 400px;
    background-color: $base-background-color;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    .topBar {
        width: calc(100% - 40px);
        display: flex;
        padding: 17px 20px 0 20px;
        justify-content: space-between;

        button {
            background-color: #68e68b;
            border-radius: 5px;
            border: none;
            padding: 3px 22px;
        }
    }

    .addTagButton {
        width: calc(100% - 40px);
        background-color: #68e68b;
        border-radius: 5px;
        padding: 3px 0;
        border: none;
        margin-top: 25px;
        margin-bottom: 15px;
    }

    .tagsList {
        width: calc(100% - 40px);
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        align-items: flex-start;
        flex-direction: column;

        .tagsMenuItem {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            span {
                min-width: 40px;
                padding: 3px 8px;
                border-radius: 5px;
                font-size: 8pt;
                text-align: center;
            }

            .tagEditTools {
                display: flex;
                gap: 10px;
                align-items: center;
            }
        }

        hr {
            width: 100%;
            height: 1px;
            background: #485063;
            border: none;
        }
    }
}

#resumeArea {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.editableAccordionsArea {
    .accordionsTitle {
        font-size: 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 5px;
    }
    
    .accordions {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .accordionAddingBar {
        width: 100%;
        display: flex;
        justify-content: flex-end;

        .accordionAddingBtn {
            margin-top: 8px;
        }
    }
}

.editableAccordionRow {
    display: grid;
    grid-template-columns: 1fr 10px 30px;
    grid-template-rows: auto;

    .editableAccordion {
        grid-column-start: 1;
        grid-column-end: 4;

        padding: 7px 10px;
        border-radius: 5px;
        font-size: 13px;
        display: grid;
        grid-template-columns: 5px calc(100% - $skill-five-stars-width - 8px - 10px) calc($skill-five-stars-width + 8px);
        grid-template-rows: auto auto;
    
        .accordionArrow {
            img {
                width: 5px;
                height: 10px;
                transition: .2s;
            }
        }
    
        .accordionText {
            margin-left: 10px;
            width: calc(100% - 20px);
            padding-left: 2px;
            padding-top: 1px;
            padding-bottom: 1px;
    
            &:focus-visible {
                outline: none;
            }
        }
    
        input.accordionText {
            padding-top: 0;
            padding-bottom: 0;
            border: 1px solid black;
            background: transparent;
        }
        
        .description {
            margin-top: 5px;
            display: none;
            grid-column-start: 2;
            grid-column-end: 4;
            margin-left: 10px;
            
            .accordionDescription {
                font-size: 10px;
                background: transparent;
                padding-top: 1px;
                padding-bottom: 1px;
                width: calc(100% - 6px);
                padding: 2px;
            }
    
            textarea.accordionDescription {
                padding-top: 0;
                padding-bottom: 0;
                border: 1px solid black;
                resize: none;
            }
    
            textarea:focus-visible {
                outline: none;
            }
        }
        
        .stars {
            display: flex;
            align-items: center;
            gap: 2px;
    
            img {
                width: $skill-star-size;
                height: $skill-star-size;
            }
        }
    
        &.closed {
            .accordionArrow img {
                transform: rotate(90deg);
            }
    
            .description {
                display: block;
            }
        }
    }

    
    .deleteBtn {
        grid-column-start: 3;
        grid-column-end: 4;

        display: none;
    }
    
    &.editing {
        .editableAccordion {
            grid-column-start: 1;
            grid-column-end: 2;
        }
        
        .deleteBtn {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}